// ./ : 현재폴더

import {useRef, useState} from 'react';

import './App.css';
import TodoHeader from './component/TodoHeader';
import TodoMain from './component/TodoMain';
import TodoList from './component/TodoList';
import TodoItem from './component/TodoItem';
import TodoStatus from './component/TodoStatus';
import TodoFooter from './component/TodoFooter';

function App() {

  const [todoArray, setTodoArray] = useState([
    {
      id:1,
      todoContent: '할일 1'
    },
    {
      id:2,
      todoContent: '할일 2'
    },
    {
      id:3,
      todoContent: '할일 3'
    }
  ]);

  const [todoInput, setTodoInput] = useState({
    todoContent: ''
  });

  const {todoContent} = todoInput;

  const changeTodo = (e)=>{

    const {name, value} = e.target

    setTodoInput({
      ...todoInput,
      [name]: value
    });
  }

  const nextId = useRef(4);

  const [number, setNumber] = useState(todoArray.length);

  const addTodo = ()=>{

    const todoObj = {
      id: nextId.current,
      todoContent
    }

    setTodoArray([...todoArray, todoObj]);

    setTodoInput({
      todoContent: ''
    });

    nextId.current++;

    setNumber(number+1);
  }

  const removeTodo = (id)=>{
    setTodoArray(todoArray.filter(function(todo){
      return(
        todo.id !== id
      );
    }));
    
    setNumber(number-1);
  }

  return (
    <>
      <TodoHeader todoContent={todoContent} changeTodo={changeTodo} addTodo={addTodo} />
      <TodoMain>
        <TodoList todoArray={todoArray} removeTodo={removeTodo} />
        <TodoStatus number={number} />
      </TodoMain>
      <TodoFooter />
    </>
  );
}

export default App;
